@import 'varible.scss';

.time-panel {
    padding: 0 20px;
    .date {
        padding: 8px 16px;
        font-weight: bold;
        cursor: pointer;
        &:hover {
            background-color: $primary-color;
            color: #fff;
        }
    }
    .day {
        position: absolute;
        top: -1.875rem;
        color: gray;
        font-size: small;
    }
}

